.advantages {
  overflow: hidden;
  position: relative;

  &__heading::before {
    content: "Преимущества";
    right: 0;
    margin: 0 auto;
  }

  > picture {
    position: absolute;
    top: 11px;
    right: 0;
    z-index: -1;
  }

  @include vp-1199 {
    > picture {
      display: none;
    }
  }

  &__container {
    padding-top: 68px;
    padding-bottom: 65px;

    @include vp-1199 {
      padding-top: 46px;
      padding-bottom: 42px;
    }

    @include vp-767 {
      padding-top: 34px;
      padding-bottom: 33px;
    }
  }

  &__heading {
    margin: 0 0 47px;
    text-align: center;

    @include vp-767 {
      margin: 0 0 29px;
    }
  }

  &__slider {
    width: 2020px;
    margin: 0 0 50px -410px;

    @include vp-1199 {
      width: 100%;
      margin: 0;
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style-type: none;

    @include vp-1199 {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      gap: 30px;
    }

    @include vp-767 {
      gap: 15px 0;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 380px;
    height: auto;
    margin-right: 30px;
    padding: 39px 20px;
    background-color: $color-default-white;
    border: 1px solid rgba(2, 102, 193, 0.1);

    @include vp-1199 {
      width: calc(47% + 5px);
      margin-right: 0;
      padding: 29px 20px;
      justify-self: center;
    }

    @include vp-767 {
      width: 100%;
      padding: 19px 10px;
    }
  }

  &__icon {
    margin: 0 0 30px;
    padding: 15px;
    color: $color-navy-blue;
    border: 1px solid $color-navy-blue;

    svg {
      display: block;
    }

    @include vp-1199 {
      margin: 0 0 26px;
      padding: 13px;

      svg {
        width: 20px;
        height: 20px;
      }
    }

    @include vp-767 {
      margin: 0 0 25px;
      padding: 11px;

      svg {
        width: 18px;
        height: 18px;
      }
    }
  }

  &__text h3 {
    margin: 0 0 15px;
    font-weight: 600;
    font-size: 18px;
    line-height: 18px;
    text-align: center;

    @include vp-1199 {
      margin: 0 0 14px;
      font-size: 16px;
      line-height: 16px;
    }

    @include vp-767 {
      margin: 0 0 15px;
      font-size: 14px;
      line-height: 14px;
    }
  }

  &__text p {
    margin-top: 0;
    font-size: 14px;
    line-height: 22px;
    text-align: center;

    @include vp-1199 {
      font-size: 12px;
      line-height: 18px;
    }
  }

  &__text > *:last-child {
    margin: 0;
  }

  &__navigation {
    display: flex;
    gap: 0 4px;
    justify-content: center;

    @include vp-1199 {
      display: none;
    }
  }
}
