/* montserrat-300 - cyrillic_latin */
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/montserrat-v25-cyrillic_latin-300.woff2") format("woff2");
}
