.education {
  overflow: hidden;

  &__heading::before {
    content: "Обучение";
  }

  &__container {
    padding-top: 62px;
    padding-bottom: 62px;

    @include vp-1199 {
      padding-top: 46px;
      padding-bottom: 45px;
    }

    @include vp-767 {
      padding-top: 27px;
      padding-bottom: 8px;
    }
  }

  &__heading {
    margin: 0 0 32px;

    @include vp-1199 {
      margin: 0 0 21px;
    }

    @include vp-767 {
      margin: 0 0 16px;
    }
  }

  &__text {
    max-width: 820px;
    margin: 0 0 56px;

    @include vp-1199 {
      max-width: 80%;
      margin: 0 0 66px;
    }

    @include vp-767 {
      max-width: 100%;
      margin: 0 0 49px;
    }
  }

  &__text p {
    margin-top: 0;

    @include vp-767 {
      font-size: 14px;
      line-height: 20px;
    }
  }

  &__text > *:last-child {
    margin: 0;
  }

  &__instructors {
    margin: 0 0 51px;

    @include vp-1199 {
      margin: 0 0 43px;
    }

    @include vp-767 {
      margin: 0 0 38px;
    }
  }

  &__subheading {
    margin: 0 0 41px;
    font-weight: 700;
    font-size: 21px;
    line-height: 21px;

    @include vp-1199 {
      font-size: 18px;
      line-height: 18px;
    }

    @include vp-767 {
      margin: 0 0 28px 4px;
      font-size: 16px;
      line-height: 16px;
    }
  }

  &__process-list {
    display: grid;
    align-items: end;
    margin: 0;
    padding: 0;
    list-style-type: none;
    gap: 50px 30px;
    grid-template-columns: repeat(4, 1fr);

    @include vp-1199 {
      gap: 90px 30px;
      grid-template-columns: repeat(2, 1fr);
    }

    @include vp-767 {
      gap: 25px 0;
      grid-template-columns: 1fr;
    }
  }

  &__process-item {
    position: relative;
    margin: 0 0 41px;
    padding: 0 0 0 40px;

    &::before {
      content: "";
      position: absolute;
      bottom: -41px;
      left: 0;
      width: 14px;
      height: 14px;
      background-color: $color-navy-blue;
      border-radius: 50%;
    }

    &::after {
      content: "";
      position: absolute;
      right: 0;
      bottom: -34px;
      left: -30px;
      height: 1px;
      background-color: rgba($color-navy-blue, 0.1);
    }

    svg {
      position: absolute;
      top: 11px;
      left: 0;
    }

    &--map {
      &::after {
        left: -50vw;
      }
    }

    &--mountain {
      &::after {
        right: -50vw;
      }
    }

    @include vp-1199 {
      margin: 0;
      padding: 0 0 0 39px;

      svg {
        top: 8px;
      }

      &--map {
        &::before {
          bottom: -23px;
        }

        &::after {
          right: -37px;
          bottom: -16px;
          left: 0;
        }
      }

      &--boots {
        &::before {
          bottom: -23px;
        }

        &::after {
          right: auto;
          bottom: -73px;
          left: 6px;
          width: 1px;
          height: 60px;
        }
      }

      &--pine-tree {
        order: 1;

        &::before {
          top: -27px;
        }

        &::after {
          display: none;
        }
      }

      &--mountain {
        &::before {
          top: -27px;
        }

        &::after {
          top: -20px;
          right: -37px;
          left: 0;
          height: 1px;
        }
      }

      &--bottom {
        align-self: start;
      }
    }

    @include vp-767 {
      margin: 0 0 0 37px;
      padding: 0 0 0 31px;
      gap: 0 6px;

      &::before {
        top: 18px;
        left: -33px;
        width: 13px;
        height: 13px;
        transform: translateY(calc(-50% + 2px));
      }

      &::after {
        top: -13px;
        right: 0;
        bottom: -13px;
        left: -27px;
        width: 1px;
        height: auto;
      }

      svg {
        width: 19px;
        height: 19px;
      }

      &--pine-tree {
        order: 0;

        &::after {
          display: block;
        }
      }

      &--bottom {
        align-items: stretch;
      }

      &:first-child::after {
        top: 18px;
      }

      &:last-child::after {
        top: -12px;
        bottom: calc(100% - 18px);
      }
    }
  }

  &__process-item p {
    margin-top: 0;

    @include vp-1199 {
      max-width: 70%;
      font-size: 14px;
      line-height: 22px;
    }

    @include vp-767 {
      max-width: 85%;
      font-size: 12px;
      line-height: 18px;
    }
  }

  &__process-item > *:last-child {
    margin: 0;
  }
}
